//== Typography ==//

//font family
@font-family:           'Helvetica Neue', 'Roboto', 'helveticaui';
@font-family-serif:     'Helvetica Neue', 'Roboto', 'helveticaui';
@icon-font:             "FontAwesome";
@base-font-size:        1.6rem;

//scale factors -- don't change
@minor-second:          1.067;
@major-second:          1.125;
@minor-third:           1.2;
@major-third:           1.25;
@perfect-fourth:        1.333;
@dim-fifth:             1.414;
@perfect-fifth:         1.5;
@minor-sixth:           1.6;
@golden-section:        1.618;

//margin ratios
@margin-top:            1em;
@margin-bottom:         0.66em;
@margin-top-neg:        -0.25em;

//lineheight ratios
@h-line-height:         1.2;
@p-line-height:         1.5;
@line-height:           @p-line-height;

//header vars
@headings-font-family:  @font-family;
@headings-font-weight:  700;
@headings-color:        inherit;
@strong-weight:         700;

//fontsize
.typeVars(@base-scale: @perfect-fourth, @base-size: @base-font-size){
	//base font size and line height
	@font-size: @base-size;
	@scale-factor: @base-scale;

	//heading font size
	@font-size-h1: round((@font-size * 10) * pow(@scale-factor, 3)) / 10;
	@font-size-h2: round((@font-size * 10) * pow(@scale-factor, 2)) / 10;
	@font-size-h3: round((@font-size * 10) * pow(@scale-factor, 1)) / 10;
	@font-size-h4: round((@font-size * 10) * pow(@scale-factor, 0.5)) / 10;
	@font-size-h5: @font-size;
	@font-size-h6: round((@font-size * 10) * pow(@scale-factor, -1)) / 10;
}
