//
// Typography
// --------------------------------------------------

.variable-font-size(@base-scale: @perfect-fourth, @base-size: @base-font-size){
	.typeVars(@base-scale, @base-size);
	body{
		font-size: @font-size;
	}

	h1,
	.h1{
		font-size: @font-size-h1;
	}
	h2,
	.h2{
		font-size: @font-size-h2;
	}
	h3,
	.h3{
		font-size: @font-size-h3;
	}
	h4,
	.h4{
		font-size: @font-size-h4;
	}
	h5,
	.h5{
		font-size: @font-size-h5;
	}
	h6,
	.h6{
		font-size: @font-size-h6;
	}

	select,
	textarea,
	input[type="text"],
	input[type="password"],
	input[type="datetime"],
	input[type="datetime-local"],
	input[type="date"],
	input[type="month"],
	input[type="time"],
	input[type="week"],
	input[type="number"],
	input[type="email"],
	input[type="url"],
	input[type="search"],
	input[type="tel"],
	input[type="color"]{
		font-size: @font-size;
	}
}

.variable-font-size(@minor-third);

